body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  min-width: 300px;
  flex-grow: 1;
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  font-size: 100px;
}

.block--1 {
  background-color: rgb(255,0,0);
}

.block--2 {
  background-color: rgb(204,0,0);
}

.block--3 {
  background-color: rgb(153,0,0);
}

.block--4 {
  background-color: rgb(102,0,0);
}

.block--5 {
  background-color: rgb(51,0,0);
}

.block--6 {
  background-color: rgb(0,0,0);

}

@media (min-width: 600px) {

  .container {
    flex-direction: row;
  }

  .block--4 {
    order: 1;
  }

  .block--3 {
    order: 2;
  }

  .block--5,
  .block--6 {
    order: 3;
  }
}

@media (min-width: 900px) {

  .block {
    flex-basis: 33.33%;
  }

  .block--4 {
    order: 5;
  }

  .block--5 {
    order: 4;
  }

}
